
import Card from 'vux/src/components/card/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Emotion from 'vux/src/components/wechat-emotion/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapState} from "vuex"
export default {
name:"topic",
props:['id'],
components: {
CellBox,
Group,
Flexbox,
FlexboxItem,
Emotion,
Divider,
XButton,
XTextarea,
Card,
Popup
},
data(){
return{
topic:"",
reply_content:"",
reply_content2:"",
isShowPopup:false,
reply_id:null

}
},
computed:{
...mapState(['isLogined'])
},
directives: {
TransferDom
},
methods:{

upOrDown(reply_id){
var app = this
this.$http.post("/reply/"+reply_id+"/ups",{
accesstoken:this.$store.state.accesstoken,
}).then(function (res) {
console.log(res.data)
var reply;
if(res.data && res.data.success){//表示点赞或者取消点赞成功
for(var i=0;i<app.topic.replies.length;i++){
if(app.topic.replies[i].id == reply_id){
reply =app.topic.replies[i]
break;
}
}
if(res.data.action == "up"){//表示点赞成功


reply.ups.push(app.$store.state.author_id)
}
if(res.data.action == "down"){//表示取消点赞成功


var index = reply.ups.indexOf(app.$store.state.author_id)
reply.ups.splice(index,1);
}
}
})
},
deCollect(){
var app = this
this.$http.post("/topic_collect/de_collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function (res) {
if(res.data.success){ //取消收藏成功,手动修改is_collect的值为false
app.topic.is_collect = false;
}
})
},

handleCollect(){
var app = this
this.$http.post("/topic_collect/collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function (res) {
if(res.data.success){ //返回true 表示修改成功,手动修改is_collect的值为true
app.topic.is_collect = true;
}
})
},

handleHide(){
this.reply_content=""
},

handlePopup(reply_id,reply_loginname){
this. isShowPopup=true
this.reply_id = reply_id
this.reply_content2 = "@" +reply_loginname+" "+this.reply_content
},
handleReply(replyType){
var app =this
var params ={
accesstoken:this.$store.state.accesstoken,
content:this.reply_content
}
if(replyType ==2){
params.reply_id = this.reply_id
params.content = this.reply_content2
}
this.$http.post("/topic/"+this.topic.id+"/replies",params).then(function (res) {

if(res.data.success){
var reply={
"id": app.reply_id,
"author": {
"loginname": app.$store.state.loginname,
"avatar_url": "https://avatars.githubusercontent.com/u/84430805?v=4&s=120"
},
"content": params.content,
"ups": [],
"create_at": "2021-05-27T06:57:24.413Z",
"reply_id": null,
"is_uped": false
}
app.topic.replies.push(reply)
app.reply_content ="" 
app.isShowPopup = false; 
}
})
}
},
beforeRouteEnter(to,from,next){
next(function (vm) {
vm.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function (res) {
console.log(res)
if(res.data.success){
vm.topic=res.data.data;
}
})
})

},
beforeRouteUpdate(to,from,next){
var app = this
this.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function (res) {
if(res.data.success){
app.topic=res.data.data;
}
})
next();
}

}



