
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import {mapState,mapGetters} from "vuex"
export default {
name:"topiclist",
props:['name'],
computed:{
...mapState(['topiclists']),
...mapGetters(['fileterTab'])
},
data(){
return{
listData:[],
page:1,//页码.第一页
limit:20,//每一页显示的数据个数
pullUpconfig:{//设置上拉刷新的配置信息(提示)
content:"上拉加载更多",
downContent:"松开加载更多",
upContent:"上拉加载更多",
loadingContent:"加载中..."
},
pulldownconfig:{//设置上拉刷新的配置信息(提示)
content:"下拉加载最新",
downContent:"松开加载最新",
downContent:"下拉加载最新",
loadingContent:"加载中..."
},
}
},
components:{
Cell,
Group,
Scroller,
Tab,
TabItem
},
methods:{
handleTab(index){
console.log(index)

this.$router.push("/topiclist/"+this.topiclists[index].name)
},
handlePullDown(){
var app = this
this.$http.get("/topics",{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
app.listData=response.data.data

app.$refs.Pullup.donePulldown()
app.page =1
}
})
},
handlePullUp(){
var app = this
this.page= this.page +1;
this.$http.get("/topics",{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
for(var i =0; i<response.data.data.length;i++){
app.listData.push(response.data.data[i])
}

app.$refs.Pullup.donePullup()
}
})
}
},


beforeRouteEnter(to,from,next){

next(function(vm){
vm.$http.get("/topics",{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(response){
console.log(response);
if(response.data.success){
vm.listData=response.data.data;
}
})
})
},

beforeRouteUpdate(to,from,next){
var app = this
app.$http.get("/topics",{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
console.log(response);
if(response.data.success){
app.listData=response.data.data;
}
})
next();

}
}



