
import Msg from 'vux/src/components/msg/index.vue'


export default{
name:"logout",
components:{Msg},
data(){
return{
buttons:[{
text:"跳转至登录",
type:"primary",
link:"/login"
}]
}
},
created(){
if(this.$store.state.isLogined == false){
return;
}

window.localStorage.clear();

this.$store.commit("mutationsClear")

this.$router.push("/topiclist/all");
this.$router.go(0);

}
}



