
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

import {mavonEditor} from "mavon-editor"
import axios from "axios"
import "mavon-editor/dist/css/index.css"
export default {
name:"edit",
props:['id'],
components:{
XButton,
XTextarea,
Group,
mavonEditor
},
data(){
return{
topic:{
title:"",
content:""
}

}
},
methods:{

editTopic(){
var app =this
this.$http.post("/topics/update",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id,
title:this.topic.title,
tab:"dev",
content:this.topic.content
}).then(function (res) {
if(res.data.success){
app.$router.back();
}
})
}
},
beforeRouteEnter(to,from,next){
next(function (vm) {
vm.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function (res) {
console.log(res)
if(res.data.success){
vm.topic=res.data.data;
}
})
})

},
beforeRouteUpdate(to,from,next){
var app = this
this.$http.get('/topic/'+to.params.id,{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function (res) {
if(res.data.success){
app.topic=res.data.data;
}
})
next();
},
uploadImg(pos,file){



var params = new FormData();
params.append("smfile",file)
axios.post("https://sm.ms/api/v2/upload",
params,{
headers: {'Content-Type': 'multipart/form-data' },
}
).then(function (res) {
console.log(res)
})


}
}



