
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name:"user",
props:['loginname'],
data(){
return{
userInfo:null,
collectInfo:null
}
},
components: {
Card,
Cell,
Group
},
beforeRouteEnter(to,from,next){

next(function(vm){
vm.$http.get("/user/"+to.params.loginname).then(function(response){
console.log(response)
vm.userInfo=response.data.data;
});
vm.$http.get("/topic_collect/"+to.params.loginname).then(function (res) {
console.log(res.data)
vm.collectInfo = res.data.data
})
})
},
beforeRouteUpdate(to,from,next){
var app = this
this.$http.get("/user/"+to.params.loginname,{
}).then(function (res) {
app.userInfo=res.data.data;
});
this.$http.get("/topic_collect/"+to.params.loginname).then(function (res) {
console.log(res.data)
app.collectInfo = res.data.data
})
next();
}

}



