import Vue from "vue"
import mutations from "./mutations"
import actions from "./actions"
import Vuex from "vuex"

Vue.use(Vuex);
const store =new Vuex.Store({
    state:{
        topiclists:[
            {name:"all",desc:"全部"},
            {name:"good",desc:"精华"},
            {name:"share",desc:"分享"},
            {name:"ask",desc:"问答"},
            {name:"dev",desc:"招聘"},
            {name:"job",desc:"测试"},
         ],
         isShow:false, //显示更多菜单
         isLogined:false,//表示登陆状态
         //登陆后的菜单
         LoginedMenu:{
           create:"发表新话题",
           center:"个人中心",
           msgs:"消息中心",
           logout:"退出"
    
         },

         //未登录的菜单
         unLoginMenu:{
           login:"登录"
         },
         loginname:"",
         accesstoken:"",
         author_id:"",
         count:""

    },
    //状态数据的计算属性
    getters:{
        moreMenu(state){
            //根据登陆状态,显示不同的更多菜单(登陆前和登陆后的菜单)
            //如果isLogined=true,表示已登录
            if(state.isLogined){
              return state.LoginedMenu //显示登录后的菜单
            }
           return state.unLoginMenu//显示未登录的菜单
          },
        fileterTab(state){
          return function(tab){
            for (let i = 0; i <  state.topiclists.length; i++) {
              const element = state.topiclists[i]
              if(element.name == tab){
                return state.topiclists[i].desc
              }
            }
          }
        }
    },
    mutations,
    actions,
})

export default store;


