
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapGetters, mapMutations, mapState} from "vuex"
import VmBackTop from "vue-multiple-back-top"
export default {
name: 'app',
data(){
return{
count:""
}
},
computed:{
...mapGetters(['moreMenu']),
isShow:{

get(){
return this.$store.state.isShow;
},
set(nval){
return this.$store.commit('mutationsIsShow',nval);
}
}
},
components:{
XHeader,
VmBackTop,
Actionsheet,
Badge
},
methods:{

getMessageCount(){
var app = this
this.$http.get("/message/count",{
params:{
accesstoken:this.$store.state.accesstoken,
}}).then(function (res) {
if(res.data.success){
app.count = res.data.data;
}
})
},

handleTitle(){
this.$router.push("/topiclist/all")
},

handelMoreMenu(){
this.isShow = true
},

handleActionSheet(menuKey,menuItem){
this.$router.push("/"+menuKey)
},
...mapMutations(['mutationsLogined','mutationsLoginname','mutationAccessToken','mutationsAuthor_id'])
},
created(){


var loginStatus=window.localStorage.getItem("loginStatus");

if(loginStatus){
this.mutationsLogined();
this.mutationsLoginname(window.localStorage.getItem("loginname"));
this.mutationAccessToken(window.localStorage.getItem("accesstoekn"));
this.mutationsAuthor_id(window.localStorage.getItem("author_id"));
var app =this;
this.getMessageCount();
window.setInterval(function () {
app.getMessageCount();
},1000*60*5)
}


}
}



