
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Msg from 'vux/src/components/msg/index.vue'

import {mapMutations,mapState} from "vuex"
export default {
name:"login",
components: {
Group,
XInput,
XButton,
Toast,
Msg,
},
computed:{
...mapState(['isLogined'])
},
data(){
return{
accesstoken:'',
isShowToast:false,
toastText:'',
buttons:[{
text:"跳转至退出",
type:"primary",
link:"/logout"
}]

}
},
methods:{
...mapMutations(['mutationsLogined','mutationsLoginname','mutationAccessToken', "mutationsAuthor_id"]),

doLogin(){
var app= this
this.$http.post('/accesstoken',{
accesstoken:this.accesstoken
}).then(function (res) {
if(res && res.data){
app.mutationsLogined();
app.mutationsLoginname(res.data.loginname);
app.mutationAccessToken(app.accesstoken);
app.mutationsAuthor_id(res.data.id);


window.localStorage.setItem("loginStatus",true);
window.localStorage.setItem("loginname",res.data.loginname);
window.localStorage.setItem("accesstoekn",app.accesstoken);
window.localStorage.setItem("author_id",res.data.id);


app.$router.push("/user/"+res.data.loginname);
app.$router.go(0);
}
}).catch(function (error) {
console.log(error.response)
app.isShowToast=true
app.toastText = error.response.data.error_msg
})
}
},
created(){
if(this.$store.state.isLogined == true){
return;
}
}
}



